body {
  font-family: "Open Sans";
}

.down {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  background-color: #ffffff;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.left {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin-left: 20%;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #ffffff;
}
.right {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin-right: 20%;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #ffffff;
}
.App {
  text-align: center;
  font-family: "Open Sans";
}
.container {
  min-height: 100vh;
}
.workCardWrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-left: 10%;
  margin-right: 10%;
  margin-top: 20px;
  margin-bottom: 20px;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}
.name {
  color: white;
  font-family: "Open Sans";
  margin: 20px;
  font-size: 5rem;
  font-weight: 800;
}
h2 {
  color: black;
  font-family: "Open Sans";
  font-size: 3vh;
  font-weight: 800;
  margin-left: 10px;
}
.title {
  color: white;
  font-family: "Open Sans";
  font-size: 1rem;
  font-weight: 800;
  margin-left: 10px;
  margin-right: 10px;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@media screen and (max-width: 600px) {
  .down {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    background-color: #ffffff;
    -webkit-flex-direction: column !important;
            flex-direction: column !important;
  }
  .workCardWrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-flex-direction: column !important;
            flex-direction: column !important;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  .left {
    margin-right: 20%;
  }
  .right {
    margin-left: 20%;
  }
  .name {
    font-size: 3rem;
  }
}

.morebutton {
  cursor: pointer;
  border: 2px white solid;
  border-width: 2px;
  border-color: white;
  padding: 10px 20px;
  color: white;
  font-family: "Open Sans";
  font-weight: 800;
  margin: 30px;
}
.morebutton:hover {
  background-color: white;
  color: black;
  transition-duration: 1s;
}

.workCard {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  width: 30%;
  margin: 20px;
  height: 300px;
  border-radius: 5px;
  border: 1px #f6f6f6 solid;
}
@media screen and (max-width: 600px) {
  .workCard {
    width: 70%;
  }
}
.showButton {
  cursor: pointer;
  background-color: black;
  color: white;
  width: 100%;
  height: 40px;
  display: -webkit-flex;
  display: flex;
  font-size: 14px;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-family: "Open Sans";
  font-weight: 600;
}
.showButton:hover {
  cursor: pointer;
  background-color: white;
  color: black;
  transition-duration: 1s;
}
.titleApp {
  font-family: "Open Sans";
  font-weight: 800;
  color: black;
  margin: 20px;
}

